.project-list {
	display: flex;
	flex: 1;
	justify-content: center;
	text-align: center;
}

.project-item {
	display: flex;
	flex-direction: column;
	margin: 0.5rem 0;
	border-radius: 9px;
	transition: box-shadow 0.25s;
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12),
		0 2px 1px -2px rgba(0, 0, 0, 0.2);
	width: 14rem;
	margin: 1rem 1rem;
	border: 1px solid black;
	background-color: #ffffff;
}

.project-item:hover {
	box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.2);
}

.project-item__title {
	flex: 1;
	padding: 1rem 1.5rem;
	font-size: 1.125rem;
	font-weight: 400;
	text-align: center;
	font-family: 'Montserrat', sans-serif;
  color: #607d8b;
}

.project-item__desc {
	display: flex;
	padding: 0 0.5rem;
	font-size: 0.75rem;
	font-weight: 500;
	line-height: 1.25rem;
	margin-bottom: 40px;
	color: black;
	font-family: 'Montserrat', sans-serif;
}

.project-item__links {
	padding: 0 0 0.75rem;
	display: flex;
	justify-content: center;
	margin-top: 50px;
	font-size: 1.4rem;
	font-weight: 400;
	border-top: 1px solid black;
	font-family: 'Montserrat', sans-serif;
}

a:hover {
	cursor: pointer;
}

.code {
	color: #607d8b;
	border-color: transparent;
	background-color: transparent;
	font-family: 'Montserrat', sans-serif;
}

.personal {
	color: black;
	font-family: 'Montserrat', sans-serif;
	font-size: 0.75rem;
	font-weight: 500;
	margin-bottom: 60px;
}

.pomodoro {
	color: black;
	font-family: 'Montserrat', sans-serif;
	font-size: 0.75rem;
	font-weight: 500;
	margin-bottom: 50px;
